.ex_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 500px;
}
.card
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card span
{
  position: absolute;
  width: 250px;
  height: 250px;
  display: block;
  background: #000;
  border-radius: 50%;
  transform-origin: 125px 0;
  transform: rotate(calc(90deg * var(--i))); 
  filter:  blur(80px);
  opacity: 0.75;
}
.card span:nth-child(1) {
  background: #01b2fe;
  transform: rotate(0deg);
}
.card span:nth-child(2) {
  background: #ff008c;
  transform: rotate(90deg);
}
.card span:nth-child(3) {
  background: #ffcd00;
  transform: rotate(180deg);
}
.card span:nth-child(4) {
  background: #00c456;
  transform: rotate(270deg);
}
.card .glass {
  position: absolute;
  width: 450px;
  height: 300px;
  background: rgba(255,255,255,0.5);
  border-radius: 20px;
  border: 1px solid #fff;
  overflow: hidden;  
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.card .glass::before
{
  content: '';
  position: absolute;
  top: 0;
  left: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.2);
  pointer-events: none;
  transform: skewX(345deg);
}
.card .glass h2{
  padding: 20px;
  font-weight: 400;
  width: 100%;
  background: rgba(255,255,255,0.2);
  color: #333;
  text-align: end;
}
.card .glass h2 {
  font-weight: 300;
  font-size: 0.75em;
  font-style: normal;

}
.red {
  width: 100px;
  height: 100px;
  background: red;
  transform : skew(0deg, 30deg);
}